.film_image {
  width: 300px;
  aspect-ratio: 2/3;
  min-height: 300px;
}

.film__title {
  padding: 0;
  margin: 0;
}
.film__desc {
  margin: 0;
  padding: 0;
}

.film__button {
  margin: 10px 30%;
  width: 40%;
  cursor: pointer;
  border: none;
  background-color: beige;
}

.film__subtitle {
  font-weight: 600;
}

@media (max-width: 500px) {
  .film_image {
    max-width: 200px;
  }
}
